/* Common Classes
---------------------------------------------------------------------------------------------------- */
/* Avatar
--------------------------------------------- */
.avatar {
  border-radius: 50%;
  float: left; }

.author-box .avatar,
.alignleft .avatar {
  margin-right: 20px; }

.alignright .avatar {
  margin-left: 20px; }

.comment .avatar {
  margin: 0 15px 20px 0; }

/* Genesis
--------------------------------------------- */
.after-entry,
.archive-description,
.author-box {
  margin-bottom: 40px; }

.after-entry {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  padding: 20px 30px; }

.after-entry .widget:last-of-type {
  margin-bottom: 0; }

.breadcrumb {
  border-bottom: 1px solid #eee;
  font-size: 16px;
  font-size: 1.6rem;
  margin-bottom: 40px;
  padding-bottom: 10px; }

.archive-description p:last-child,
.author-box p:last-child {
  margin-bottom: 0; }

/* Search Form
--------------------------------------------- */
.search-form {
  overflow: hidden; }

.entry-content .search-form {
  margin-bottom: 40px;
  width: 50%; }

.post-password-form input[type="submit"],
.search-form input[type="submit"] {
  margin-top: 10px; }

.widget_search input[type="submit"] {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  width: 1px; }

/* Titles
--------------------------------------------- */
.archive-description .entry-title,
.archive-title,
.author-box-title {
  font-size: 20px;
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 10px; }

.entry-title {
  font-size: 30px;
  font-size: 3rem;
  margin-bottom: 10px; }

.entry-title a,
.sidebar .widget-title a {
  color: #333;
  text-decoration: none; }

.entry-title a:focus,
.entry-title a:hover {
  color: #0073e5; }

.widget-title {
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 20px; }

/* WordPress
--------------------------------------------- */
.aligncenter {
  text-align: center; }

a.aligncenter img {
  display: block;
  margin: 0 auto; }

a.alignnone {
  display: inline-block; }

.alignleft {
  float: left;
  text-align: left; }

.alignright {
  float: right;
  text-align: right; }

a.alignleft,
a.alignnone,
a.alignright {
  max-width: 100%; }

img.centered,
.aligncenter {
  display: block;
  margin: 0 auto 30px; }

img.alignnone,
.alignnone {
  margin-bottom: 15px; }

a.alignleft,
img.alignleft,
.wp-caption.alignleft {
  margin: 0 20px 20px 0; }

a.alignright,
img.alignright,
.wp-caption.alignright {
  margin: 0 0 20px 20px; }

.gallery-caption,
.wp-caption-text {
  font-size: 14px;
  font-size: 1.4rem;
  font-weight: 600;
  margin: 0;
  text-align: center; }

.entry-content p.wp-caption-text {
  margin-bottom: 0; }

.entry-content .wp-audio-shortcode,
.entry-content .wp-playlist,
.entry-content .wp-video {
  margin: 0 0 30px; }

@media only screen and (min-width: 768px) {
  .alignfull {
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    width: auto;
    max-width: 1000%; }
  .alignwide {
    margin-left: calc(25% - 25vw);
    margin-right: calc(25% - 25vw);
    width: auto;
    max-width: 1000%; }
  .alignwide img,
  .alignfull img {
    display: block;
    margin: 0 auto; } }

/* Widgets
---------------------------------------------------------------------------------------------------- */
.widget {
  margin-bottom: 40px; }

.widget p:last-child,
.widget ul > li:last-of-type {
  margin-bottom: 0; }

.widget ul > li {
  margin-bottom: 10px; }

.widget ul > li:last-of-type {
  padding-bottom: 0; }

.widget ol > li {
  list-style-position: inside;
  list-style-type: decimal;
  padding-left: 20px;
  text-indent: -20px; }

.widget li li {
  border: 0;
  margin: 0 0 0 30px;
  padding: 0; }

.widget_calendar table {
  width: 100%; }

.widget_calendar td,
.widget_calendar th {
  text-align: center; }

/* Featured Content
--------------------------------------------- */
.featured-content .entry {
  border-bottom: 1px solid #eee;
  margin-bottom: 20px; }

.featured-content .entry:last-of-type {
  border-bottom: none;
  margin-bottom: 0; }

.featured-content .entry-image {
  height: 75px;
  width: 75px; }

.featured-content .entry-title {
  font-size: 16px;
  font-size: 1.6rem;
  margin-bottom: 5px;
  margin-top: 10px; }

/* Plugins
---------------------------------------------------------------------------------------------------- */
/* Jetpack
--------------------------------------------- */
#wpstats {
  display: none; }

/* Skip Links
---------------------------------------------------------------------------------------------------- */
.genesis-skip-link {
  margin: 0; }

.genesis-skip-link .skip-link-hidden {
  display: none;
  visibility: hidden; }

.genesis-skip-link li {
  height: 0;
  list-style: none;
  width: 0; }

/* Display outline on focus */
:focus {
  color: #333;
  outline: #ccc solid 1px; }

/* Site Header
---------------------------------------------------------------------------------------------------- */
.site-header {
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  padding: 0 30px; }

/* Title Area
--------------------------------------------- */
.title-area {
  float: left;
  padding-bottom: 25px;
  padding-top: 25px; }

.wp-custom-logo .title-area {
  max-width: 350px;
  padding-bottom: 5px;
  padding-top: 5px; }

.site-title {
  font-size: 20px;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1;
  margin-bottom: 0; }

.site-title a,
.site-title a:focus,
.site-title a:hover {
  color: #333;
  text-decoration: none; }

.site-description,
.wp-custom-logo .site-title {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important; }

@media only screen and (min-width: 960px) {
  .site-header {
    position: fixed;
    width: 100%;
    z-index: 9999; } }

/* Site Navigation
---------------------------------------------------------------------------------------------------- */
.genesis-nav-menu {
  clear: both;
  line-height: 1;
  width: 100%; }

.genesis-nav-menu .menu-item {
  display: block;
  float: none;
  position: relative; }

.genesis-nav-menu a {
  color: #333;
  display: block;
  font-size: 15px;
  font-size: 1.5rem;
  font-weight: 400;
  outline-offset: -1px;
  padding-bottom: 12px;
  padding-top: 12px;
  text-decoration: none; }

.genesis-nav-menu a:focus,
.genesis-nav-menu a:hover,
.genesis-nav-menu .current-menu-item > a,
.genesis-nav-menu .sub-menu .current-menu-item > a:focus,
.genesis-nav-menu .sub-menu .current-menu-item > a:hover {
  color: #0073e5;
  text-decoration: none; }

.genesis-nav-menu .sub-menu,
.genesis-nav-menu .sub-menu a {
  width: 100%; }

.genesis-nav-menu .sub-menu {
  clear: both;
  display: none;
  left: -9999px;
  margin: 0;
  opacity: 1;
  padding-left: 15px;
  position: static;
  z-index: 99; }

.genesis-nav-menu .sub-menu a {
  background-color: #fff;
  font-size: 14px;
  font-size: 1.4rem;
  position: relative;
  word-wrap: break-word; }

.genesis-nav-menu .menu-item:focus,
.genesis-nav-menu .menu-item:hover {
  position: relative; }

.genesis-nav-menu .menu-item:hover > .sub-menu {
  left: auto;
  opacity: 1; }

/* Responsive Menu
--------------------------------------------- */
.menu .menu-item:focus {
  position: static; }

.menu .menu-item > a:focus + ul.sub-menu,
.menu .menu-item.sfHover > ul.sub-menu {
  left: auto;
  opacity: 1; }

.genesis-responsive-menu {
  display: none;
  padding-bottom: 15px;
  position: relative; }

.menu-toggle,
.sub-menu-toggle {
  background-color: transparent;
  border-width: 0;
  color: #333;
  display: block;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;
  visibility: visible; }

.menu-toggle:focus,
.menu-toggle:hover,
.sub-menu-toggle:focus,
.sub-menu-toggle:hover {
  background-color: transparent;
  border-width: 0;
  color: #0073e5; }

.menu-toggle {
  float: right;
  line-height: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 15px 0;
  position: relative;
  z-index: 1000; }

.menu-toggle.activated::before {
  content: "\f335"; }

.site-header .menu-toggle::before {
  float: left;
  margin-right: 5px;
  position: relative;
  text-rendering: auto;
  top: 1px; }

.sub-menu-toggle {
  float: right;
  padding: 9px 10px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100; }

.sub-menu .sub-menu-toggle {
  padding: 12px 10px; }

.sub-menu-toggle::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-transform: rotate(0);
  -ms-transform: rotate(0);
  transform: rotate(0);
  transition: transform .25s ease-in-out; }

.sub-menu-toggle.activated::before {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg); }

/* Header Menu
--------------------------------------------- */
.nav-primary {
  clear: left;
  width: 100%; }

