  font-weight: 600;
  margin-bottom: 4px;
  padding: 8px 12px;
  text-decoration: none; }

.archive-pagination li a:focus,
.archive-pagination li a:hover,
.archive-pagination li.active a {
  background-color: #333;
  color: #fff; }

/* Entry Comments
--------------------------------------------- */
.comment-respond,
.entry-comments,
.entry-pings {
  font-size: 18px;
  font-size: 1.8rem;
  margin-bottom: 40px; }

.comment-list li {
